// bubbling

const list = document.querySelector("#list");
const form = document.querySelector("#new-tweet-form");
const textarea = document.querySelector("#textarea");
const submitBtn = document.querySelector(".submit");
// generate 
const generateId = () =>
  Math.floor((1 + Math.random()) * 0x10000)
    .toString(16)
    .substring(1);

const tweets = [
  {
    tweetId: generateId(),
    username: "darmirs99@gmail.com",
    text: "Hey, my little boy",
    isLiked: true
  }
];

const createTweets = ({ tweetId, username, text, isLiked }) => {
  const li = document.createElement("li");
  // Ð´Ð¾Ð´Ð°Ñ”Ð¼Ð¾ ÑƒÐ½Ñ–ÐºÐ°Ð»ÑŒÐ½Ð¸Ð¹ Ð´Ð°Ñ‚Ð° Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚
  li.dataset.tweetId = tweetId;
  li.innerHTML = `
  <i class="fa-solid fa-xmark delete-icon"></i>
      <p class="username">${username}</p>
      <p class="tweets">${text}</p>
        <div class="buttons">
          <a href="#" class="like-button ${
            isLiked ? "like-button-active" : ""
          }">
            <i class="fas fa-heart"></i>
          </a>
        </div>
  `;

  return li;
};
//Ð¿Ñ–Ð´Ð¿Ð¸ÑÑƒÑ”Ð¼Ð¾ÑÑ Ð½Ð° Ð´Ð²Ð° Ñ–Ð½Ð¿ÑƒÑ‚Ð° Ñ– Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÑÑ”Ð¼Ð¾ Ñ‡Ð¸ Ñ” Ð½Ð° Ð´Ð²Ð¾Ñ… Ñ–Ð½Ð¿ÑƒÑ‚Ð°Ñ… Ð·Ð½Ð°Ñ‡ÐµÐ½Ð½Ñ

textarea.addEventListener("input", (e) => {
  const counter = document.querySelector(".counter");
  const inputValueLength = e.target.value.length;
  const characterLimit = 50;
  // increment counter, when typing
  const inputValue = e.target.value;
  if (inputValueLength <= characterLimit) {
    counter.style.color = "";
  } else {
    counter.style.color = "red";
  }
  counter.textContent = `${inputValueLength}/${characterLimit}`;
  // if field is empty -disable button
  if (inputValue !== "") {
    submitBtn.removeAttribute("disabled");
  } else {
    submitBtn.setAttribute("disabled", true);
  }
});

form.addEventListener("submit", (e) => {
  e.preventDefault();
  const data = new FormData(e.target); // Ð²Ð¸Ñ‚ÑÐ³Ð°Ñ”Ð¼Ð¾ Ð´Ð°Ð½Ñ– Ð· Ð½Ð°ÑˆÐ¾Ñ— Ñ„Ð¾Ñ€Ð¼Ð¸
  const tweetObj = {
    tweetId: generateId(),
    username: "darmirs99@gmail.com",
    text: data.get("text"), // Ð¿Ð¾ Ñ–Ð¼ÐµÐ½Ñ– Ð¿Ð¾Ð»Ñ Ð²Ð¸Ñ‚ÑÐ³Ð°Ñ”Ð¼Ð¾
    isLiked: false
  };
  //save added tweet to localstorage
  saveTweets(tweetObj);
  list.append(
    createTweets(tweetObj)
    //text", "email" Ñ†Ðµ Ð¼Ð¸ Ð·Ð²ÐµÑ€Ð°Ñ‚Ñ”Ð¼ÑÑ Ð·Ð° Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð¾Ð¼ name
  );
  textarea.value = "";
  submitBtn.setAttribute("disabled", true);
});

list.addEventListener("click", (event) => {
  event.preventDefault();
  //!!!! ÑˆÑƒÐºÐ°Ñ”Ð¼Ð¾ Ð½Ð°Ð¹Ð»Ð¸Ð¶Ñ‡Ð¸Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚, ÑÐºÐ¸Ð¹ Ð·Ð°Ð´Ð¾Ð²Ð»ÑŒÐ½ÑÑ” Ð²ÐºÐ°Ð·Ð°Ð½Ð¸Ð¹ ÑÐµÐ»ÐµÐºÑ‚ÐµÑ€
  const buttonElement = event.target.closest(".like-button");
  //ÑÐºÑ‰Ð¾ Ð¹Ð¾Ð³Ð¾ Ð·Ð½Ð°Ð¹ÑˆÐ»Ð¸, Ñ‚Ð¾ Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°Ñ”Ð¼Ð¾ ÐºÐ»Ð°Ñ
  if (buttonElement !== null) {
    buttonElement.classList.toggle("like-button-active");
  }

  const deleteIcon = event.target.closest(".delete-icon");
  if (deleteIcon !== null) {
    const li = event.target.closest("li");
    removeTweets(li.dataset.tweetId);
    li.remove();
  }
});

function getTweets() {
  const localTweets = localStorage.getItem("tweets");
  if (localTweets === null) {
    return tweets;
  }
  return JSON.parse(localTweets);
}

function saveTweets(tweet) {
  const tweets = getTweets();
  tweets.push(tweet);
  localStorage.setItem("tweets", JSON.stringify(tweets));
}
function removeTweets(tweetId) {
  const tweets = getTweets();
  localStorage.setItem(
    "tweets",
    JSON.stringify(tweets.filter((tweet) => tweet.tweetId !== tweetId))
  );
}

const renderTweets = () => {
  const tweets = getTweets();
  tweets.forEach((tweet) => {
    list.append(createTweets(tweet));
  });
};

renderTweets();
